body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*=========================
          COLORS
==========================*/
.container {
  padding: 0 .8rem; }

body {
  background-color: #393f4d;
  font-family: 'Kalam', cursive;
  color: white; }

body::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 700;
  content: '\F46D';
  opacity: .1;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -125px;
  z-index: -1;
  font-size: 300px; }

.toDoList-h1 {
  text-align: center;
  padding: 1.5rem 0;
  background-color: red;
  margin: 0;
  border: 1px solid black;
  border-bottom: 0; }

.taskCounter {
  position: fixed;
  right: 22%;
  top: 28.7%;
  display: flex;
  height: 65px;
  width: 65px;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  background-color: black; }

.addTaskPanel {
  padding: 0;
  display: flex; }

.inputAddTask {
  font-family: 'Kalam', cursive;
  width: 80%;
  padding: 1rem;
  padding-left: 15%;
  font-size: 1.2rem;
  border: 1px solid black; }

.inputAddTask:focus {
  background-color: #FFFAF0; }

.buttonAddTask {
  border: 1px solid black;
  border-left: 0;
  padding: 1rem 0;
  font-size: 1.2rem;
  font-family: 'Kalam', cursive;
  background-color: white;
  color: red;
  text-align: center;
  width: 20%;
  -webkit-transition: 0.5s;
  transition: 0.5s; }

.buttonAddTask:hover {
  cursor: pointer;
  border: 0;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 0px 50px 10px rgba(0, 0, 0, 0.3);
  -webkit-transition: 0.5s;
  transition: 0.5s; }

.inputAddTask,
.buttonAddTask {
  border-top: 0; }

button::-moz-focus-inner {
  border: 0; }

.searchPanel {
  padding: 0;
  display: flex; }

.inputSearch {
  font-family: 'Kalam', cursive;
  width: 100%;
  padding: 1rem;
  padding-left: 15%;
  font-size: 1.2rem;
  border: 1px solid black; }

.inputSearch:focus {
  background-color: #FFFAF0; }

.taskItemContainer {
  display: flex;
  flex-flow: row nowrap; }

.taskItemContainer:nth-child(odd) .taskItem {
  background-color: red; }

.taskItemContainer:nth-child(odd) .taskItem:hover {
  background-color: #bd2626; }

.taskItemContainer:nth-child(even) .taskItem {
  background-color: #F96714; }

.taskItemContainer:nth-child(even) .taskItem:hover {
  background-color: #df5403; }

.taskItem {
  font-family: 'Kalam', cursive;
  width: 80%;
  padding: 1rem;
  padding-top: 1.3rem;
  padding-bottom: 1.3rem;
  padding-left: 15%;
  font-size: 1.2rem;
  border: 1px solid black;
  border-top: 0;
  -webkit-transition: .5s;
  transition: .5s;
  align-self: center; }

.taskItem:hover {
  -webkit-transition: .5s;
  transition: .5s;
  cursor: pointer; }

.removeTaskItemButton {
  border: 1px solid black;
  border-top: 0;
  border-left: 0;
  padding: 1rem 0;
  font-size: 1.2rem;
  font-family: 'Kalam', cursive;
  background-color: #F8F8FF;
  color: black;
  text-align: center;
  width: 20%;
  -webkit-transition: .5s;
  transition: .5s; }

.removeTaskItemButton:hover {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  border: 0;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: grey;
  color: white;
  -webkit-transition: .5s;
  transition: .5s;
  box-shadow: 0px 0px 50px 10px rgba(0, 0, 0, 0.3);
  cursor: pointer; }

.removeTaskItemButton::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 400;
  font-size: 2rem;
  content: '\F2ED'; }

.checkItem {
  text-decoration: line-through; }

.checkItem::before {
  font-family: "Font Awesome 5 Free";
  font-weight: 700;
  content: '\F07A';
  position: absolute;
  left: 2%;
  height: 15px;
  width: 7px;
  margin-top: 5px; }

.taskList {
  margin: 0;
  padding: 0;
  list-style: none; }

